@tailwind base;
@tailwind components;
@tailwind utilities;
* {
  font-family: "Poppins", sans-serif;
}

body {
  scroll-behavior: smooth;
}

.label:after {
  content: "";
  width: 40px;
  height: 7px;
  background-color: #27cfd2;
  position: absolute;
  top: 120px;
  left: -40px;
}

.label:before {
  content: "";
  width: 40px;
  height: 7px;
  background-color: #27cfd2;
  position: absolute;
  top: 120px;
  right: -40px;
}

@media screen and (max-width: 1280px) {
  html {
    font-size: 93.75%;
  }
  .label:after {
    width: 84px;
    height: 7px;
    top: -46px;
    transform: rotate(90deg);
    left: 0;
    right: 0;
    margin: 0 auto;
  }

  .label:before {
    width: 100%;
    height: 7px;
    top: 200px;
    transform: rotate(90deg);
    left: 0;
    right: 0;
    margin: 0 auto;
  }
}

.hamburger-inner,
.hamburger-inner:after,
.hamburger-inner:before {
  background-color: #9c9aa5 !important;
}

.ripple {
  width: 1rem; /* control the size */
  background: #ff0; /* control the color here */
}
.ripple,
.ripple::before,
.ripple::after {
  content: "";
  display: grid;
  grid-area: 1/1;
  aspect-ratio: 1;
  border-radius: 50%;
  box-shadow: 0 0 0 0 #ff03; /* and here, 3 is the transparency */
  animation: r 3s linear infinite var(--s, 0s);
}
.ripple::before {
  --s: 1s;
}
.ripple::after {
  --s: 2s;
}

@keyframes r {
  to {
    box-shadow: 0 0 0 6rem #0000;
  }
}

@media screen and (max-width: 768px) {
  html {
    font-size: 87.5%;
  }
  .custom-border {
    position: relative;
  }
  .custom-border:after {
    content: "";
    width: calc(100% + 54px);
    position: absolute;
    bottom: 0;
    left: -27px;
    right: 0;
    margin: 0 auto;
    height: 1px;
    background: #e5e5e5;
  }
}
